@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Open+Sans);
/*---Card Detailed---*/
.card-detailed {
  text-align: center; }
  .card-detailed p#job {
    margin-top: -25px; }

/*---Base---*/
/*------------------------Mixins-----------------------*/
body {
  margin: 0px; }

.container {
  display: flex; }
  @media only screen and (max-width: 48em) {
    .container {
      display: block; } }

img {
  max-width: 100%;
  height: auto; }

.main {
  background: #131313; }
  .main .container {
    flex-direction: row;
    align-items: center; }
  .main .first-col {
    flex-grow: 1; }
  .main .second-col {
    flex-grow: 4; }

.card {
  background: #fff;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 0 20px #eee; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Josefin Sans", cursive;
  line-height: 1.6;
  color: #fff !important; }

h1 {
  font-size: 36px; }
  @media only screen and (max-width: 48em) {
    h1 {
      font-size: 28px; } }

h2 {
  font-size: 32px; }
  @media only screen and (max-width: 48em) {
    h2 {
      font-size: 26px; } }

h3 {
  font-size: 28px; }
  @media only screen and (max-width: 48em) {
    h3 {
      font-size: 22px; } }

p,
ol,
ul,
a {
  font-family: "Open Sans", sans-serif;
  line-height: 1.6;
  color: #a7a7a7 !important; }


/*---Column---*/
.col {
  display: block;
  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 1; }

/*---Card-Overview---*/
.card-overview {
  padding: 15px;
  border-bottom: 3px solid #2d2d2d; }
  .card-overview:focus, .card-overview:hover {
    background: #4a4a4a;
    cursor: pointer;
    transition: .2s; }
  .card-overview .col {
    flex: inherit; }
    .card-overview .col img {
      padding-right: 30px; }

/*---Content---*/
.content {
  background: #131313;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .content h1, .content h2, .content h3, .content h4, .content h5, .content h6, .content p {
    color: #333; }
  .content .intro {
    margin: 0 auto;
    text-align: center; }
    .content .intro h1 {
      margin-bottom: 0px; }


/*---Card-Overview---*/
input {
  width: 100%;
  height: 35px; }

/*---Sidebar---*/
.sidebar {
  background: #333; }
  .sidebar h1, .sidebar h2, .sidebar h3, .sidebar h4, .sidebar h5, .sidebar h6, .sidebar p {
    color: #fff; }

